@import "https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400&family=Work+Sans:ital,wght@0,100..900;1,100..900&family=Yellowtail&display=swap";
:root {
  --content-width: 1216px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --font-family-primary: "Roboto", sans-serif;
  --font-family-secondary: "Open Sans", sans-serif;
  --font-family-third: "Yellowtail", cursive;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --color-white: #fff;
  --color-white-50: #ffffffe6;
  --color-white-75: #f6f4ef;
  --color-white-100: #f9f8f8;
  --color-white-125: #eff6f1;
  --color-white-150: #ececec;
  --color-black: #000;
  --color-black-300: #2c2e35;
  --color-black-600: #0009;
  --color-blue-600: #142a33b3;
  --color-gray-200: #ababab;
  --color-gray-400: #525c60;
  --color-orange-400: #f4a261;
  --color-red-400: #e63946;
  --color-green-300: #1ab837;
  --color-green-400: #26532b;
  --radius-btn: 16px;
  --radius-main: 30px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  font-family: var(--font-family-primary);
  font-weight: var(--fw-700);
  letter-spacing: 0;
  background-color: var(--color-white);
  font-optical-sizing: auto;
  font-variation-settings: "wdth" 100;
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1;
}

.page__body {
  min-height: var(--vh);
  min-width: 320px;
}

.page.open {
  overflow: hidden;
}

.container {
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;
}

.site-container {
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.background-light {
  background-color: var(--color-white-75);
}

.logo {
  width: 100%;
  max-width: 391px;
}

.circle {
  position: relative;
}

.circle:before {
  content: "";
  background-color: var(--color-gray-400);
  border-radius: 50%;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  width: 100%;
  height: 100dvh;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  font-weight: var(--fw-800);
  color: var(--color-white);
  text-transform: capitalize;
  width: fit-content;
  line-height: 1.2;
  overflow: visible;
}

h1 span, h2 span, h3 span {
  text-transform: lowercase;
}

h1 {
  color: var(--color-white-125);
  font-size: clamp(1.75rem, 1rem + 3.75vw, 70px);
}

h2, .title {
  color: var(--color-green-400);
  font-size: 50px;
}

@media only screen and (width <= 576px) {
  h2, .title {
    font-size: 32px;
  }
}

h3 {
  color: var(--color-green-400);
  width: 100%;
  font-size: 25px;
}

p {
  font-family: var(--font-family-secondary);
  font-size: 18px;
  line-height: 1.6;
  font-weight: var(--fw-400);
  letter-spacing: 0;
}

.header {
  z-index: 15;
  background-color: var(--color-white);
  width: 100%;
  padding: 24px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header__inner {
